*{
    padding:0;
    margin: 0;
  }
  body{
    background-color: dimgray;
  }
 .about-1{
  margin: 30px;
  padding: 5px;
 }
 .about-1 h1{
  text-align: center;
  color: black;
  font-weight: bold;
 }
 .about-1 p{
  text-align: center;
  padding: 3px;
  color: #fff;
 }
 .about-item{
  margin-bottom: 20px;
  margin-top: 50px;
  background-color: white;
  padding: 80px,30px;
  box-shadow: 0 0 9px rgba(0,0,0 .6);
 }
 .about-item i{
  font-size: 43px;
  margin: 0;
 }
 .about-item h3{
  font-size: 25px;
  margin-bottom: 10px;
 }
 .about-item hr{
  width: 46px;
  height: 3px;
  background-color: aqua;
  margin: 0 auto;
  border: nome;
 }
 .about-item p{
  margin-top: 20px;
 }
 .about-item:hover{
  background-color: rgb(36, 54, 54);
 }
 .about-item:hover i,
 .about-item:hover h3,
 .about-item:hover p{
    color: #fff;
 }
 .about-item:hover hr{
  background-color: #fdfdfd;
 }
 .about-item:hover i{
  transform: translateY(-20px);
 }
 .about-item:hover i,
 .about-item:hover h3,
 .about-item:hover hr{
  transition: all 400ms ease-in-out;
 }
 .container h1{
  color: black;
  font-weight: bold;
 }
 .style-item:hover{
  background-color: rgb(36, 54, 54);
 }
 .style-item:hover p,
 .style-item:hover h5
 {
  color:#fff;
 }